import React from 'react';
import './App.css';
import Home from "./Component/Home"
import ResultLKG from "./Component/LKG/ResultLKG"
import MainResultLKG from "./Component/LKG/MainResultLKG"
import ResultUKG from "./Component/UKG/ResultUKG"
import MainResultUKG from "./Component/UKG/MainResultUKG"
import MainResult1 from "./Component/1st/MainResult1"
import MainResult9 from "./Component/9th/MainResult9"
import MainResult5 from "./Component/5th/MainResult5"
import MainResult7 from "./Component/7th/MainResult7"
import MainResult8 from "./Component/8th/MainResult8"
import Result1 from "./Component/1st/Result1"
import Result9 from "./Component/9th/Result9"
import Result7 from "./Component/7th/Result7"
import Result5 from "./Component/5th/Result5"
import Result8 from "./Component/8th/Result8"
// <!-- import { useReactToPrint } from 'react-to-print'; -->
import {
  BrowserRouter as Router,
  Routes,
  Route,
  
} from "react-router-dom";
function App() {
  
  return (
    <Router  basename={process.env.PUBLIC_URL}>  
     
      
    <Routes>
      <Route exact path="/sbps_result" element={<Home />} />
      <Route exact path="/sbps_result/ResultLKG" element={<ResultLKG />} />
      <Route exact path="/sbps_result/ResultUKG" element={<ResultUKG />} />
      <Route exact path="/sbps_result/MainResultLKG" element={<MainResultLKG />} />
      <Route exact path="/sbps_result/Result1" element={<Result1 />} />
      <Route exact path="/sbps_result/Result9" element={<Result9 />} />
      <Route exact path="/sbps_result/Result5" element={<Result5 />} />
      <Route exact path="/sbps_result/Result7" element={<Result7 />} />
      <Route exact path="/sbps_result/Result8" element={<Result8 />} />
      <Route exact path="/sbps_result/MainResultUKG" element={<MainResultUKG />} />
      <Route exact path="/sbps_result/MainResult1" element={<MainResult1 />} />
      <Route exact path="/sbps_result/MainResult9" element={<MainResult9 />} />
      <Route exact path="/sbps_result/MainResult5" element={<MainResult5 />} />
      <Route exact path="/sbps_result/MainResult7" element={<MainResult7 />} />
      <Route exact path="/sbps_result/MainResult8" element={<MainResult8 />} />
     
  </Routes>

    </Router>
   
  );
}

export default App;
